        content: '';
        display: block;
        position: absolute;
        top: 50%;
        transform: translate3d(0, -50%, 0);
        width: 5px;
        height: 52px;
        border-radius: 30px;
        background-color: #00fffe;
        box-shadow: 0 0 10px #00fffe;
      }
