<template>
  <!-- <div class="header-wrapper" @dblclick="changeView">
    <div class="header-l header">
      <ul class="nav-l nav">
        
      </ul>
    </div>
    <div class="header-r header">
      <ul class="nav-r nav">
      </ul>
    </div>
  </div> -->
</template>

<script type="text/javascript">
// **** 组件名称 大驼峰 ****
// * 本地公用变量 公用函数 *
// ***** 第三方 组件库 *****
// ***** 本地 公用组件 *****
// ** 当前组件的 子组件等 **

export default {
  name: 'mainnav',
  data() {
    return {
      timeDate:{
        d:'',
        t:'',
        w:''
      },
      listOpen:false,
      // 标题文字
      title: ''
    }
  },
  created:function () {
    setInterval(()=>{
      this.setTimeDate();
      this.changeTitle()
    },1000);
  },
  methods: {
    setTimeDate:function() {
      let now = new Date();
      let weekArr = ['日','一','二','三','四','五','六'];

      this.timeDate.d = now.getFullYear() + '年' + (now.getMonth()+1) + '月'+now.getDate() + '日';
      let time = [now.getHours(), now.getMinutes(), now.getSeconds()].map((num)=>{
         if (num<10){
           num = '0'+num;
         }
         return num;
      })
      this.timeDate.t = time.join(':');
      this.timeDate.w = '星期'+weekArr[now.getDay()];
    },
    changeTitle() {
      let flag = this.$route.name
      switch(flag) {
        case 'homepage':
          this.title = '生态云'
          break
        case 'water':
          this.title = '水环境'
          break
        case 'atmosphere':
          this.title = '大气环境'
          break
        case 'pollution':
          this.title = '污染源'
          break
        case 'dataview':
          this.title = '数据总览'
          break
        case 'service':
          this.title = '惠民服务'
          break
        default:
          this.title = '生态云'
      }
    },
    changeView() {
      let target = window.document.body
      let flag = target.style.transform === `scale3d(0.5, 0.5, 1)`
      flag ? target.style.transform = `scale3d(1, 1, 1)` : target.style.transform = `scale3d(0.5, 0.5, 1)`
    },
    back() {
      this.$router.go(-1)
    },
    goHome() {
      this.$router.push('/homepage')
    }
  },
  computed: {
    isBack() {
      return this.$route.name === 'homepage'
    }
  }
}
</script>

<style lang="scss" scoped>

@import "~common/css/variable";
@import "~common/css/mixin";

.header-wrapper {
  @include flex(space-between);
  position: relative;
  width: 100%;
  // padding: 0 145px;
  padding: 0 80px;
  height: 60px;
  box-sizing: border-box;
  box-shadow: 0 15px 50px rgba(0,0,0,.2);
  background: #002144;
  background-image: url('nav-bg-light.png');
  background-size: cover;
  background-repeat: repeat-x;
  background-position: top left;
  /*background-image: url('nav-bg-light.png');*/
  /*background-size: cover;*/
  /*background-repeat: no-repeat;*/
  /*background-position: top left;*/
}
.header {
  @include flex(space-between);
  height: 100%;
  flex: 1 0 30%;
  color: #04c2ff;
}

.nav {
  @include flex();
  flex: 1 0 auto;
  height: 100%;
  font-size: 24px;
  font-weight: bold;
  &.nav-l {
    @include flex(flex-end);
    .item {
      margin-left: -26px;
      background: url('nav-leftmenu.png');
      &:hover, &.active {
        background: url('nav-leftmenu-active.png');
      }
    }
  }
  &.nav-r {
    .item {
      margin-right: -26px;
      background: url('nav-rightmenu.png');
      &:hover, &.active {
        background: url('nav-rightmenu-active.png');
      }
    }
  }
  .item {
    @include flex(space-around, center);
    position: relative;
    z-index: 1;
    width: 258px;
    text-align: center;
    height: 100%;
    cursor: pointer;
    @include animation();
    &:hover, &.active {
      color: #00feff;
    }
  }
}

@font-face {
  font-family: '方正正中黑体';
  src: url('../../common/font/mainnavFont.ttf');
}


.title {
  @include flex(space-around);
  position: relative;
  flex: 0 0 800px;
  margin: 0 -40px;
  height: 75px;
  font-size: 36px;
  font-weight: bold;

  color: #fff; // #fff
  letter-spacing: 2px;
  background: url('./nav-bg-middle-light.png') no-repeat;
  background-size: 100% 100%;
  background-position: center bottom;
  cursor: pointer;
}


.time {
  font-size: 18px;
  color: $cl-txt-hl;
}

.weather {
  @include flex(space-between);
  // width: 141px;
  font-size: 18px;
  > * {
    margin-right: 37px;
    &:last-child {
      margin-right: 0;
    }
  }
  .icon-xiala {
    cursor: pointer;
    transition: all 0.2s;
  }
  .iconXialaOpen {
    transform: rotateZ(180deg);
  }
  .icon-xiala:hover {
    color: rgb(27, 137, 173);
  }

  .icon-forward {
    color: #fff;
    cursor: pointer;
    @include animation();
    &:hover {
      color: rgb(27, 137, 173);
    }
  }
}

.temperature {
  .icon-sun {
    font-size: 22px;
    color: #ffff01;
  }
}

.platform-list {
  position: absolute;
  top: 60px;
  right: 80px;
  transform:scaleY(0);
  overflow: hidden;
  transform-origin:top;
  transition: all 0.3s;
  ul {
    background-color: rgba(0, 33, 68, 0.8);
    border: 2px solid #0070fd;
    .platform-item {
      cursor: pointer;
      border-top: 1px solid #0070fd;
      color: #00bdff;
      font-size: 18px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      padding: 0 10px;
    }
    .platform-item:first-child {
      border-top: 0;
    }
    .platform-item:hover {
      background-color: rgba(0, 33, 68, 1) !important;
    }
  }
}

.platformOpen {
  transform:scaleY(1) !important;
  z-index: 999 !important;
}

</style>



