import Vue from 'vue'
import Router from 'vue-router'
import nDataview from 'pages/newdataview/newdataview'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/dataview'
    },

    {
      path: '/dataview',
      name: 'dataview',
      component: nDataview
    }
  ]
})



